.mt-slide-view{background-color:#fff;overflow:hidden}.mt-slide-view-wrapper{transition-timing-function:cubic-bezier(0.18, 0.89, 0.32, 1);transition-property:transform;position:relative}.mt-slide-view-cell{position:absolute;top:0;height:100%}.mt-slide-view-cell--left{display:flex;flex-direction:row;justify-content:flex-start;align-items:stretch;left:0;transform:translate3d(-100%, 0, 0)}.mt-slide-view-cell--right{display:flex;flex-direction:row;justify-content:flex-start;align-items:stretch;right:0;transform:translate3d(100%, 0, 0)}