.mt-slider-modal{--modal-mask-bg-color: rgba(0, 0, 0)}.mt-slider .rax-slider-pagination,.mt-slider .rax-slider-swipe{flex-direction:row}.mt-slider-modal-container{background:transparent}.mt-slider-control{position:fixed;z-index:var(--elevation-4);box-sizing:border-box;left:0;top:0;width:100%;padding:0 13.33333vw;background-color:rgba(0,0,0,.6)}.mt-slider-close-wrapper{width:13.33333vw;height:13.33333vw;position:absolute;left:0;top:0;z-index:calc(var(--elevation-4) + 1);display:flex;flex-direction:row;justify-content:center;align-items:center}.mt-slider-close{color:var(--color-white)}.mt-slider-pagination{color:var(--color-white);text-align:center;line-height:13.33333vw}.mt-slider-open-item{display:flex;flex-direction:row;justify-content:center;align-items:center;width:100vw;height:100%}.mt-slider-open-item--image{background-size:contain;background-repeat:no-repeat;background-position:center}